body {
  background-color: #f6f6f6;
}

.text-shopping {
  color: #111;
  letter-spacing: -1.92px;
  font-family: DM Sans;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px;
}

.text-list {
  color: #4f2ee8;
  letter-spacing: -1.92px;
  margin-left: .2em;
  font-family: DM Sans;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px;
}

.main-title-shoplist-hidden {
  display: flex;
}

.book-card-position {
  text-align: left;
}

.book-card {
  text-align: left;
  background: #fff;
  border: 2px solid #4f2ee866;
  border-radius: 16px;
  max-width: 400px;
  margin-bottom: 20px;
  padding: 20px;
  position: relative;
  box-shadow: 0 4px 8px #0000001a;
}

#cover {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.book-details {
  text-align: left;
}

#buy-links a {
  color: #007bff;
  margin-top: 10px;
  text-decoration: none;
  display: block;
}

.container-btn-delete-book {
  background-color: #4f2ee8;
  border: 1px groove #4f2ee8;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

#btn-delete-book {
  fill: #fff;
}

.amazon-logo, .book-icon {
  filter: grayscale();
  margin-right: 10px;
}

.container-logo-position {
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 15px;
  right: 15px;
}

.container-btn-arrow-left-left, .container-btn-arrow-left {
  background-color: #111;
  border: 1px groove #111;
  border-radius: 50%;
  align-items: center;
  width: 44px;
  height: 44px;
  display: flex;
}

#btn-arrow-left-1, #btn-arrow-left-2 {
  fill: #b4afaf;
  color: #b4afaf;
  transform: rotate(90deg);
}

#btn-arrow-left-2 {
  margin-left: -15px;
}

#page-1, #page-2, #page-3, #page-more {
  color: #111;
  background-color: #f6f6f6;
  border: 1px groove #000;
  border-radius: 50%;
  align-items: center;
  width: 44px;
  height: 44px;
  font-family: DM Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 22px;
  display: flex;
}

.container-btn-arrow-right-right, .container-btn-arrow-right {
  background-color: #eac645;
  border: 1px groove #000;
  border-radius: 50%;
  align-items: center;
  width: 44px;
  height: 44px;
  display: flex;
}

#btn-arrow-right-1, #btn-arrow-right-2 {
  fill: #000;
  color: #000;
  transform: rotate(-90deg);
}

#btn-arrow-right-2 {
  margin-left: -15px;
}

.container-btns-slider, .container-btns-pages {
  text-align: center;
  display: flex;
}

